@import url(https://fonts.googleapis.com/css?family=Leckerli+One);
@import url(https://fonts.googleapis.com/css?family=Sue+Ellen+Francisco);
@import "bower_components/susy/sass/susy";
@import "bower_components/compass-breakpoint/stylesheets/breakpoint";


/*MIXINS*/

$browser-context: 16; // Default

$susy: (
  columns: 12,
);

@function em($pixels, $context: $browser-context) {
  @return #{$pixels/$context}em
}

@mixin clearfix() {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

@mixin accordion(){
  span{
    color: white;
    padding: 15px;
    cursor: pointer;
  }

  .content{
    max-height: 0;
    overflow:hidden;
    display:none;
    transition: max-height 300ms ease;
  }

  li{
    &.active{
    > .content{
      max-height: 1000px;
      transition: max-height 300ms ease;
    }
  }
  }
}

@mixin pagination{
  display:flex;
  justify-content: center;
  li{
    width: 35px;
    height: 35px;
    padding: 0;
    margin: 5px;
    text-align:center;
    line-height: 32px;
    display:none;

    &.disabled {
      pointer-events:none;
    }

    &:last-child, &:first-child{
    }

    .prev{
      margin-left: 0;
    }

    .next{
      margin-right: 0;
      float:right;
    }

    span, a{
      font-size: em(12);
      text-align:center;
    }
  }
}

@mixin vertical-align() {
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.animated{
  visibility:visible !important;
}

.removedelay{
  animation-delay: 0s !important;
}

.pop-cover{
  padding:0;
  margin:0;
  width:100%;
  height:100%;
  background:rgba(#0c2d4e, 0.5);
  position:fixed;
  display:none;
}

@mixin pop-up(){
  @extend %vertical-align;
  width: 50%;
  height: 200px;
  background: white;
  margin: 0 auto;
  position:fixed;
  left:0;
  right:0;

  .close{
    width:15px;
    height:15px;
    background: red;
    position:absolute;
    right:0;
    top:0;
    cursor: pointer;
  }
}

.img-holder{
  overflow: hidden;
}

%li-clear{
  display:inline-block;
  margin: 0 0 0 -4px;
  list-style-type: none;
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  border-radius: $radius;
  background-clip: padding-box;
}

@mixin flip-h {
    -moz-transform: scaleX(-1);
    -o-transform: scaleX(-1);
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    filter: FlipH;
    -ms-filter: "FlipH";
}

@mixin flip-v{
  -moz-transform: scaleY(-1);
    -o-transform: scaleY(-1);
    -webkit-transform: scaleY(-1);
    transform: scaleY(-1);
    filter: FlipV;
    -ms-filter: "FlipV";
}

