/*STYLE GUIDE*/

.page-styleguide{
  section{
    padding: 2% 5%;
    @include container(100%);
    title{
      border-bottom: 1px solid black;
      margin-bottom: 15px;
    }
  }
}

.sg-colors{
  list-style-type:none;

  li{
    @include gallery(1 of 10);
    border: 1px solid black;
    padding: 5px;

    @each $current-color in $sg-colorlist {
    $i: index($sg-colorlist, $current-color);
      &:nth-child(#{$i}){
        figure{
          background: $current-color;
        }
        figcaption{
          color: $current-color;
        }
      }
    }

    figure{
      width: 100%;
      height: 50px;
      margin:0;
    }

    figcaption{
      text-align:center;
    }
  }
}


