    top: 0;
    width: 51%;
    height: 100%;
    background: #222222;
    z-index: 1000;
  }
  #loader {
    z-index: 1001; /* anything higher than z-index: 1000 of .loader-section */
  }
  h1 {
      color: #EEEEEE;
  }

  .loader-section.section-left {
    left: 0;
  }
    .loader-section.section-right {
    right: 0;
  }
  
}

#progress-bar {
  @include vertical-align;
  height: 2px;
  display: block;
  width: 100%;
  margin: 0 auto;
  background: transparent;
  text-align: center;
  position: absolute;
  z-index: 5000;
  left: 0;
  right: 0;

}

#progress-bar > #progress {
  width: 0%;
  height: 100%;
  background: white;
}

#progress-bar > p {
  color: white;
  font-family: helvetica, arial;
  font-size: 50px;
  font-weight: bold;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -10px;
}